<template>
	<div>
		<v-list subheader>
			<v-subheader>Recent chat</v-subheader>
			<v-list-tile
			v-for="item in items"
			:key="item.title"
			avatar
			@click=""
			>
				<v-list-tile-avatar>
					<img :src="item.avatar">
				</v-list-tile-avatar>

				<v-list-tile-content>
					<v-list-tile-title v-html="item.title"></v-list-tile-title>
				</v-list-tile-content>

				<v-list-tile-action>
					<v-icon :color="item.active ? 'teal' : 'grey'">chat_bubble</v-icon>
				</v-list-tile-action>
			</v-list-tile>
		</v-list>

		<v-divider></v-divider>

		<v-list subheader>
			<v-subheader>Previous chats</v-subheader>

			<v-list-tile
			v-for="item in items2"
			:key="item.title"
			avatar
			@click=""
			>
				<v-list-tile-avatar>
					<img :src="item.avatar">
				</v-list-tile-avatar>

				<v-list-tile-content>
					<v-list-tile-title v-html="item.title"></v-list-tile-title>
				</v-list-tile-content>
			</v-list-tile>
		</v-list>
	</div>
</template>

<script>
// import { VList, VSubheader } from 'vuetify';

export default {
	name: 'HelloWorld',
	data () {
		return {
			msg: 'Welcome to Your Vue.js App',
			items: [
				{ active: true, title: 'Jason ddddOner', avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg' },
				{ active: true, title: 'Ranee Carlson', avatar: 'https://cdn.vuetifyjs.com/images/lists/2.jpg' },
				{ title: 'Cindy Baker', avatar: 'https://cdn.vuetifyjs.com/images/lists/3.jpg' },
				{ title: 'Ali Connors', avatar: 'https://cdn.vuetifyjs.com/images/lists/4.jpg' }
			],
			items2: [
				{ title: 'Travis Howard', avatar: 'https://cdn.vuetifyjs.com/images/lists/5.jpg' }
			]
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



